@charset "UTF-8";

/* Fonts */

@font-face {
  font-family: "Winston";
  src: url("../fonts/Winston/Winston-Italic.woff2") format("woff2"), url("../fonts/Winston/Winston-Italic.woff") format("woff"), url("../fonts/Winston/Winston-Italic.ttf") format("truetype") url("../fonts/Winston/Winston-Italic.eot?#iefix") format("embedded-opentype"), url("../fonts/Winston/Winston-Italic.eot");
  font-weight: normal;
  font-style: italic;
  font-display: swap;
}

@font-face {
  font-family: "Winston";
  src: url("../fonts/Winston/Winston-Regular.woff2") format("woff2"), url("../fonts/Winston/Winston-Regular.woff") format("woff"), url("../fonts/Winston/Winston-Regular.ttf") format("truetype") url("../fonts/Winston/Winston-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Winston/Winston-Regular.eot");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Winston";
  src: url("../fonts/Winston/Winston-Bold.woff2") format("woff2"), url("../fonts/Winston/Winston-Bold.woff") format("woff"), url("../fonts/Winston/Winston-Bold.ttf") format("truetype") url("../fonts/Winston/Winston-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/Winston/Winston-Bold.eot");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

/* Variables */

:root {
  --color-white: #FFFFFF;
  --color-black: #000000;
  --color-black-25: rgba(0, 0, 0, 0.25);
  --color-gray: #c2c2c2;
  --color-dark: #3E3E3E;
  --text-red-gradient: linear-gradient(208.77deg, #A42E32 -24.41%, #E52A31 92.24%), linear-gradient(0deg, #FFFFFF, #FFFFFF);
  --gradient-btn: linear-gradient(208.77deg, #A42E32 -24.41%, #E52A31 92.24%), linear-gradient(0deg, #FFFFFF, #FFFFFF);
  --gradient-main-screen: linear-gradient(0deg, #A42E32 0%, #E52A31 100%);
  --color-red: #A42E32;
  --color-blue: #00b2ff;
  --invalid: #f30008;
  --color-yellow: #dcc101;
  --color-green: #8ABC36;
  --font-winston: "Winston", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

/* Base */

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

img {
  max-width: 100%;
}

/* Animation */

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes rotate-revert {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(-360deg);
  }
}

@keyframes lines-transform {
  from {
    transform: translateX(-5%);
  }

  to {
    transform: translateX(-32%);
  }
}

/* Burger-menu */

@keyframes rotate-burger-before {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(45deg);
  }
}

@keyframes rotate-burger-after {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-45deg);
  }
}

/* Spinner */

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.gs-reveal {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.5s ease-in-out 0.2s, visibility 0.5s ease-in-out 0.2s;
  will-change: opacity, visibility;
}

.gs-reveal.is-revealed {
  opacity: 1;
  visibility: visible;
}

.a-reveal-left {
  transform: translate3d(-100px, 0, 0);
  opacity: 0;
  transition: transform ease-in-out 0.5s 0.5s, opacity 0.5s ease-in-out 0.5s;
}

.a-reveal-left.is-revealed {
  transform: translate3d(0, 0, 0);
  opacity: 1;
}

.a-reveal-right {
  transform: translate3d(100px, 0, 0);
  opacity: 0;
  transition: transform ease-in-out 0.5s 0.5s, opacity 0.5s ease-in-out 0.5s;
}

.a-reveal-right.is-revealed {
  transform: translate3d(0, 0, 0);
  opacity: 1;
}

.a-reveal-element {
  transform: translate3d(0, -50px, 0);
  opacity: 0;
  transition: transform ease-out 0.5s 1.3s, opacity 0.5s ease-out 1.3s;
}

.a-reveal-element.is-revealed {
  transform: translate3d(0, 0, 0);
  opacity: 1;
}

.a-reveal-opacity {
  opacity: 0;
  transition: opacity 0.5s ease-out 1.3s;
}

.a-reveal-opacity.is-revealed {
  opacity: 1;
}

/* Base Classes */

/* Debug */

/* Containers */

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.container-full {
  padding-left: 30px;
  padding-right: 30px;
}

/* Hide */

.hide {
  display: none !important;
}

/* Position */

.left {
  text-align: left;
}

.center {
  text-align: center;
}

.right {
  text-align: right;
}

/* Background */

.bg--white {
  background-color: var(--color-white);
}

.bg--blue {
  background-color: var(--color-blue);
}

.bg--light-blue {
  background-color: var(--color-light-blue);
}

.block-padding {
  padding: 75px 0;
}

.block-padding-large {
  padding: 120px 0 150px;
}

.block-padding-small {
  padding: 50px 0 50px;
}

/* Links */

.link {
  text-decoration: none;
  transition: ease 0.3s;
  color: var(--color-dark);
}

.link:hover {
  color: var(--color-red);
}

/* Title */

.title {
  font-family: var(--font-winston);
  font-style: normal;
  margin: 0;
}

.title--big {
  font-size: calc(30px + 20 * ((100vw - 360px) / 1080));
}

@media screen and (min-width: 1440px){

  .title--big{
    font-size: 50px;
  }
}

@media screen and (max-width: 360px){

  .title--big{
    font-size: 30px;
  }
}

.title--large {
  font-size: calc(30px + 10 * ((100vw - 360px) / 1080));
}

@media screen and (min-width: 1440px){

  .title--large{
    font-size: 40px;
  }
}

@media screen and (max-width: 360px){

  .title--large{
    font-size: 30px;
  }
}

.title--pre-large {
  font-size: calc(30px + 5 * ((100vw - 360px) / 1080));
}

@media screen and (min-width: 1440px){

  .title--pre-large{
    font-size: 35px;
  }
}

@media screen and (max-width: 360px){

  .title--pre-large{
    font-size: 30px;
  }
}

.title--medium {
  font-size: calc(20px + 5 * ((100vw - 360px) / 1080));
}

@media screen and (min-width: 1440px){

  .title--medium{
    font-size: 25px;
  }
}

@media screen and (max-width: 360px){

  .title--medium{
    font-size: 20px;
  }
}

.title--tiny {
  font-size: 18px;
}

.title--black {
  color: var(--color-black);
}

.title--white {
  color: var(--color-white);
}

.title--red-gradient {
  background: var(--text-red-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.title--w-bold {
  font-weight: bold;
}

.title--uppercase {
  text-transform: uppercase;
}

.title--indent-small {
  margin-bottom: 30px;
}

.title--indent-large {
  margin-bottom: 60px;
}

/* Text */

.text {
  font-family: var(--font-winston);
  font-style: normal;
  margin: 0;
}

.text--black {
  color: var(--color-black);
}

.text--white {
  color: var(--color-white);
}

.text--dark {
  color: var(--color-dark);
}

.text--large {
  font-size: calc(25px + 5 * ((100vw - 360px) / 1240));
}

@media screen and (min-width: 1600px){

  .text--large{
    font-size: 30px;
  }
}

@media screen and (max-width: 360px){

  .text--large{
    font-size: 25px;
  }
}

.text--medium {
  font-size: calc(16px + 4 * ((100vw - 1024px) / 416));
}

@media screen and (min-width: 1440px){

  .text--medium{
    font-size: 20px;
  }
}

@media screen and (max-width: 1024px){

  .text--medium{
    font-size: 16px;
  }
}

.text--normal {
  font-size: calc(14px + 2 * ((100vw - 1024px) / 416));
}

@media screen and (min-width: 1440px){

  .text--normal{
    font-size: 16px;
  }
}

@media screen and (max-width: 1024px){

  .text--normal{
    font-size: 14px;
  }
}

.text--small {
  font-size: 12px;
}

.text--w-regular {
  font-weight: normal;
}

.text--w-bold {
  font-weight: 700;
}

.text--italic {
  font-style: italic;
}

.text--uppercase {
  text-transform: uppercase;
}

