/* Burger-menu */

.burger-menu {
  width: 50px;
  height: 50px;
  cursor: pointer;
  display: none;
  align-items: center;
  z-index: 102;
}

.burger-menu__line,
.burger-menu__line-two {
  display: block;
  position: relative;
  width: 50px;
  height: 3px;
  background-color: #3C4E64;
}

